// Site header

.site-header {
  align-items: center;
  justify-content: space-between;
  height: 70px;
  position: relative;
  @include clearfix;

  @include media-query($on-palm) {
    flex-direction: column;
    height: auto;
    .site-title {
      margin: 20px;
      height: 50px;
      padding-left: 0;
      svg {
        height: 50px; } }
    .site-nav {
      width: 100%;
      display: flex;
      .page-link {
        flex: 1 1 auto;
        text-align: center;
        border-top: 2px solid $grey-color-dark;
        &:first-of-type {
          border-left: none; } } } } }

.site-title {
  display: inline-block;
  margin: 0;
  height: 40px;
  line-height: default;
  padding-left: 25px;
  transform: scale(1);
  transition: all .15s ease-in-out;
  &:hover {
    transform: scale(1.1); }
  svg {
    width: auto;
    height: 40px; } }

.site-nav {
  line-height: 48px;
  order: 2;
  display: flex;
  .page-link {
    margin: 0;
    font-size: 0.875em;
    font-weight: 700;
    letter-spacing: 0.05em;
    text-transform: uppercase;
    display: inline-block;
    color: $text-color;
    padding: 10px 25px;
    border-left: 2px solid $grey-color-dark;
    transition: all .15s ease;
    text-decoration: none;
    &:hover {
      color: $raw-orange;
      text-decoration: none;
      background-color: #FFF; } } }


