// Page content
.page-content {
  padding: $spacing-unit 0; }

.page-heading {
  font-size: 20px; }

.post-list {
  margin-left: 0;
  list-style: none;

  > li {
    margin-bottom: $spacing-unit; } }

.post-meta {
  font-size: $small-font-size;
  color: #AAA; }

.post-link {
  display: block;
  font-size: 24px; }

// Posts

.post-header {
  padding: 15px 25px;
  background-color: #FFF; }

.post-title {
  font-size: 2em;
  font-weight: 700;
  letter-spacing: 0.01em;
  line-height: default;
  margin: 0;
  span {
    color: #BBB;
    font-weight: 400; }
  @include media-query($on-laptop) {
    font-size: 1.5em;
    line-height: 1.2em; } }

.post-content {
  width: 100%;
  height: auto;
  display: flex;
  .flyer {
    width: 25%;
    padding: 20px;
    border-right: 2px solid $grey-color-dark; }
  .content {
    width: 75%;
    padding: 20px;
    margin: 0 auto;
    background-color: #FFF;
    p {
      font-size: 1.2em; }
    @include media-query($on-palm) {
      font-size: 0.8em;
      padding-right: 20px; }
    hr {
      margin: 20px 0;
      border: none;
      background-color: #222;
      height: 2px; } }

  @include media-query($on-palm) {
    flex-direction: column;
    .flyer {
      width: 100%;
      border-right: none;
      padding-bottom: 0;
      background-color: #FFF; }
    .content {
      width: 100%; } }

  h1 {
    display: inline-block;
    font-size: 1.6em;
    background-color: #FFF;
    margin-bottom: 40px;

    h2 {
      font-size: 1.2em !important; }

    h3 {
      font-size: 26px;

      @include media-query($on-laptop) {
        font-size: 22px; } }

    h4 {
      font-size: 20px;

      @include media-query($on-laptop) {
        font-size: 18px; } } } }
