#rawlove {
  width: 100%; }

.rawlove {
  width: 100%;
  display: flex;
  flex-direction: row;
  border-bottom: 2px solid $grey-color-dark;
  margin: 0;
  padding: 0;
  &:last-of-type {
    border-bottom: none; }
  .cover {
    width: 40%;
    border-right: 2px solid $grey-color-dark; }
  .embed {
    flex: 1 1 auto;
    min-height: 400px;
    background-color: #FFF; }
  @include media-query($on-palm) {
    flex-direction: column;
    .cover {
      width: 100%;
      border-right: none;
      padding: 10px 10px 0 10px;
      background-color: #FFF; }
    .embed {
      height: 500px; } } }
