.home_heading {
  padding: 15px;
  background-color: #FFF;
  h1 {
    display: block;
    margin: 0 auto;
    font-size: 2.3em;
    font-weight: 700;
    text-transform: uppercase;
    letter-spacing: 0.05em;
    text-align: center;
    line-height: 1.2em;
    @include media-query($on-palm) {
      font-size: 2em; } } }


.home {
  display: flex;
  background-color: #FFF;
  .cover {
    width: 40%; }
  @include media-query(960px) {
    flex-direction: column;
    justify-content: space-around;
    text-align: center;
    .cover {
      padding: 10px 10px 0 10px;
      border-right: 2px solid $grey-color-dark;
      img {
        width: 500px; }
      @include media-query(960px) {
        border-right: none; } }
    .embed {
      iframe {
        height: 650px; } } } }

.embed {
  padding: 20px;
  flex: 1 1 auto;
  iframe {
    display: block;
    width: 1px;
    min-width: 100%;
    max-width: 960px;
    height: 100%;
    min-height: 300px;
    margin: 0 auto; }
  @include media-query($on-palm) {
    padding: 10px; } }
