// Site footer

.site-footer {
  align-items: center;
  justify-content: space-between;
  // height: 72px
  @include media-query($on-palm) {
    flex-direction: column;
    justify-content: space-around;
    text-align: center;
    height: auto; }
  .site-nav {
    order: 0;
    @include media-query($on-palm) {
      width: 100%;
      display: flex;
      @include clearfix;
      height: 50px;
      border-bottom: 2px solid $grey-color-dark; } }
  .site-nav > .page-link {
    padding: 25px;
    font-size: 1.5em;
    height: 50px;
    border-left: none;
    border-right: 2px solid $grey-color-dark;
    display: flex;
    align-items: center;
    justify-content: space-around;
    @include media-query($on-palm) {
      flex: 1 1 auto;
      text-align: center;
      padding: 10px;
      line-height: default;
      height: auto;
      &:last-of-type {
        border-right: none; } } }
  .footer-text {
    padding-right: 25px;
    order: 2;
    p {
      font-size: 0.75em;
      margin-bottom: 0;
      letter-spacing: 0.05em;
      text-align: right;
      a {
        color: $text-color;
        text-decoration: underline;
        margin-left: 5px;
        &:hover {
          color: $raw-orange; } } }
    @include media-query($on-palm) {
      padding-right: 0;
      padding: 20px;
      p {
        text-align: center; } } } }
