.live-post-list {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  width: 100%;
  list-style: none;
  padding: 0;
  margin: 0;
  .live-post {
    width: 50%;
    background-size: cover;
    height: 300px;
    display: flex;
    align-items: flex-end;
    position: relative;
    border-bottom: 2px solid $grey-color-dark;
    &:nth-of-type(odd) {
      border-right: 2px solid $grey-color-dark; }
    &:last-of-type, &:nth-last-of-type(2) {
      border-bottom: none; }
    &:nth-of-type(2) {
      border-bottom: none; }
    @include media-query(960px) {
      width: 100%;
      border-right: none;
      border-top: none;
      border-bottom: 2px solid $grey-color-dark !important;
      &:nth-of-type(odd) {
        border-right: none; }
      &:last-of-type {
        border-bottom: none !important; } }
    h2 {
      font-weight: 700;
      margin: 0;
      color: #FFF;
      letter-spacing: 0.0375em;
      .post-link {
        position: relative;
        left: -3px;
        font-size: 1.2em !important;
        line-height: 1.2em;
        color: $text-color;
        @include media-query(960px) {
          font-size: 1em !important; } } }
    .description {
      background-color: #FFF;
      padding: 17px 40px 25px 25px;
      position: relative;
      z-index: 1;
      border-top: 2px solid $grey-color-dark;
      border-right: 2px solid $grey-color-dark;
      @include media-query(960px) {
        width: 100%;
        padding: 17px 25px 25px 25px;
        border-right: none; }
      .post-meta {
        font-size: 1.1em;
        @include media-query(960px) {
          font-size: 1em; } } }
    .live-post-overlay {
      background-color: $brand-color;
      position: absolute;
      top: 0;
      right: 0;
      bottom: 0;
      left: 0;
      opacity: 0.8; } } }
